.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

/* helper  */

.flexrow {
  display: flex;
  flex-direction: row;
}

.f-just-center {
  justify-content: center;
}
.f-align-center {
  align-items: center;
}
.flexcol {
  display: flex;
  flex-direction: column;
}
.btw {
  justify-content: space-between;
}
